import React, { useState, useEffect } from "react";
import { getUser, removeUserSession } from "../../utils/Common"; 
import API from "../../utils/api";

export default function Home(props) {
  const [company, setCompany] = useState([]);
  const handleLogout = () => {
    removeUserSession();
    props.history.push("/login");
  };

  useEffect(() => {
    API.getData("/Company/get?companyId=" + getUser().companyId)
    .then((res) => {
      if (res.status === 200) { 
        
          setCompany(res.data);      
      }
    })
    .catch(function (err) {
      console.error("Error de conexion " + err);
    });

  },[]);
  
  return (
    
    <div>
      <br />
      <section className="container d-flex align-items-center justify-content-center ">
        <div className="inner">
          <br />
          <header className="special">
          {company.map((item) => (
            <h5 key={item.id}>{item.mottoPrimary}</h5>))}
          </header>
          <div className="highlights">
            <section>
              <div className="content2">
                <header>
                  <a href="/communication" className="icon most-visited-log">
                    <img src="/images/comunicaciones.png" width="70px" className="size-effect" />
                  </a>
                  <br />
                  <br />
                  <h6>Mis comunicaciones</h6>
                  <br />
                  <br />
                </header>
              </div>
            </section>
            <section>
              <div className="content2">
                <header>
                  <a href="/Payroll" className="icon most-visited-log" data-prefix="index">
                    <img src="/images/ComprobantesDePagos.png" width="70px" className="size-effect" />
                  </a>
                  <br />
                  <br />
                  <h6>Comprobantes de Pago</h6>
                  <br />
                  <br />
                </header>
              </div>
            </section>
            <section>
              <div className="content2">
                <header>
                  <a href="/carnet" className="icon most-visited-log" data-prefix="index">
                    <img src="/images/politicas.png" width="70px" className="size-effect" />
                  </a>
                  <br />
                  <br />
                  <h6>Mi Carnet</h6>
                </header>
              </div>
            </section>

            <section>
              <div className="content2">
                <header>
                  <a href="/benefits" className="icon most-visited-log" data-prefix="index">
                    <img src="/images/pensandoenti.png" width="70px" className="size-effect" />
                  </a>
                  <br />
                  <br />
                  <h6>Pensando en tí</h6>
                  <br />
                  <br />
                </header>
              </div>
            </section>
          </div>
        </div>
      </section>
    </div>
  );
}
