import {ShowInfoMessage} from "../utils/CommonFunctions";

// return the user data from the session storage
export const getUser = () => {
    const userStr = sessionStorage.getItem('user');
    if (userStr) return JSON.parse(userStr);
    else return null;
  }
  
  // return the token from the session storage
  export const getToken = () => {
    return sessionStorage.getItem('token') || null;
  }
  
  // remove the token and user from the session storage
  export const removeUserSession = () => {
       sessionStorage.clear();
       console.clear();
  }
  
  // set the token and user from the session storage
  export const setUserSession = (token, user) => {
    sessionStorage.setItem('token', token);
    sessionStorage.setItem('user', JSON.stringify(user));
  }

  export const setPolicieSession = ()=>{
    localStorage.setItem('PoliciePending',btoa(JSON.stringify({EmployeeNumber: getUser().employeeNumber, pathUrl: window.location.pathname + window.location.search})) )
  }
  export const getPolicieSession = () =>{
    if(getUser() !== null){
      if(localStorage.getItem('PoliciePending') !== null){
        let dataResult =  JSON.parse(atob(localStorage.getItem('PoliciePending')));
        if(dataResult.EmployeeNumber === getUser().employeeNumber && window.location.pathname !== '/genericpolicies'){
            ShowInfoMessage( "<i class='fa fa-exclamation-triangle faa-flash animated red'></i>&nbsp;<span class='red'>¡Debe leer y aceptar Política!</span>");
            setTimeout(() => {window.location.href = dataResult.pathUrl; }, 3500);           
        }
      }
    }

  }
  export const removePolicieSession = () =>{
    localStorage.removeItem('PoliciePending');
  }