import React, { useState, useEffect } from "react";
import API from "../../utils/api";
import { getToken, setUserSession } from "../../utils/Common";
import { SetCssByCompany } from "../../CssbyCompany/CommonCSS";

export default function Login(props) {
  const handleLogin = () => {
    setError(null);
    setLoading(true);
    API.postData("auth/login", {
      username: username.value,
      password: password.value,
    })
      .then((response) => {
        setLoading(false);
        setUserSession(response.data.token, response.data.user);
        SetCssByCompany();
        setTimeout(() => {
          window.location.reload(false);
        }, 500);
      })
      .catch((error) => {
        setLoading(false);
        console.log(error);
        //if (error.response.status === 401) setError(error.response.data.message);
        //else setError("Something went wrong. Please try again later.");
      });
  };

  const useFormInput = (initialValue) => {
    const [value, setValue] = useState(initialValue);

    const handleChange = (e) => {
      setValue(e.target.value);
    };
    return {
      value,
      onChange: handleChange,
    };
  };

  const [loading, setLoading] = useState(false);
  const username = useFormInput("");
  const password = useFormInput("");
  const [error, setError] = useState(null);

  return (
    <div className="login_container">
      <h4 className="text-center">Somos Corripio - Administrador</h4>
      <div id="myform" className="form_container">
        <hr />
        <div className="form_group">
          <label className="classspan">C&eacute;dula</label>

          <input
            type="text"
            {...username}
            className="txtbox"
            placeholder="ejemplo: 00000000000"
          />
        </div>
        <div className="form_group">
          <label className="classspan">Contraseña</label>
          <input
            type="password"
            {...password}
            className="txtbox"
            placeholder="********"
          />
        </div>
        {error && (
          <>
            <small style={{ color: "red" }}>{error}</small>
            <br />
          </>
        )}
        <br />
        <div className="form_group">
          <button
            type="button"
            className="mybt btn btn-outline-danger text-wrap"
            onClick={handleLogin}
            disabled={loading}
          >
            {loading ? "Cargando..." : "ENTRAR"}
          </button>
        </div>
        <div className="forget">
          <hr />
          <a
            className="lform fa faa-flash animated faa-slow login-blue"
            href="#"
          >
            ¿Olvidaste tu Contraseña?
          </a>
        </div>
      </div>

      <div id="forget" className="form_container">
        <div className="form_group">
          <label class="classspan">
            Introduce tu cédula y te enviaremos un enlace por correo electrónico
            para crear una nueva contraseña.
          </label>
          <input
            type="text"
            className="txtbox"
            placeholder="Coloque su cédula"
          />
        </div>
        <div className="form_group">
          <br />
          <button
            type="button"
            className="mybt btn btn-outline-danger text-wrap"
          >
            RESTABLECER
          </button>
        </div>
        <div className="forget">
          <hr />
          <a className="fform btn btn-info" href="#">
            Entrar
          </a>
        </div>
      </div>
    </div>
  );
}
